<template>
  <div style="max-width: 1000px" class="mx-auto">
    <v-card elevation="1">
      <v-img
        src="https://gstatic.com/classroom/themes/Math.jpg"
        gradient="to top right, rgba(100,115,201,.33), rgba(25,32,72,.7)"
        max-height="200px"
        dark
      >
        <v-card-title> {{ mapel.name }} </v-card-title>
        <v-card-subtitle class="white--text">
          @{{ mapel.username }}
        </v-card-subtitle>
      </v-img>
      <v-card-text>
        <p v-show="toggleDesc">
          {{
            mapel.description || "Silahkan isi deskripsi mata pelajaran anda"
          }}
        </p>
        <v-form @submit.prevent="changeDesc">
          <v-text-field
            label="Ganti deskripsi"
            :value="mapel.description"
            v-show="!toggleDesc"
          >
            <template v-slot:append>
              <v-btn type="submit" text color="primary" icon>
                <v-icon>mdi-send</v-icon>
              </v-btn>
            </template>
          </v-text-field>
        </v-form>
        <v-btn
          @click="toggleDesc = !toggleDesc"
          small
          depressed
          outlined
          color="purple"
        >
          {{ toggleDesc ? "Ubah" : "Batal" }}
        </v-btn>
      </v-card-text>
    </v-card>
    <div class="d-flex mt-5">
      <v-btn
        to="/materi/tambah"
        :small="$vuetify.breakpoint.xsOnly"
        tile
        color="purple"
        dark
      >
        <v-icon :small="$vuetify.breakpoint.xsOnly" dark left
          >mdi-book-plus-multiple-outline</v-icon
        >
        materi baru
      </v-btn>
      <v-spacer></v-spacer>
      <v-btn
        to="/quiz/tambah"
        :small="$vuetify.breakpoint.xsOnly"
        tile
        color="purple"
        dark
      >
        quiz baru
        <v-icon :small="$vuetify.breakpoint.xsOnly" dark right
          >mdi-clipboard-plus-outline</v-icon
        >
      </v-btn>
    </div>
    <p class="mt-5 text-h5">Materi Anda</p>
    <div v-if="length > 0">
      <v-expansion-panels multiple v-model="expand">
        <v-expansion-panel v-for="(bab, i) in mapel.bab" :key="i">
          <v-expansion-panel-header>
            <span class="text-h6">
              {{ bab.babTitle }}
            </span>
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <div v-if="bab.materi.length > 0">
              <div
                v-for="(materi, j) in bab.materi"
                class="grey--text"
                :key="j"
              >
                <p class="text-caption"></p>
                {{ materi.materiTitle }}
                <ul>
                  <li v-for="(subMateri, k) in materi.subMateri" :key="k">
                    {{ subMateri.title }}
                  </li>
                </ul>
              </div>
            </div>
            <div v-else>
              <v-alert
                class="text-center"
                border="bottom"
                color="pink darken-1"
                dark
              >
                Anda belum memposting materi untuk BAB ini. Silahkan membuat
                materi baru!
              </v-alert>
            </div>
          </v-expansion-panel-content>
        </v-expansion-panel>
      </v-expansion-panels>
    </div>
    <v-alert
      v-else
      class="text-center"
      border="bottom"
      color="pink darken-1"
      dark
    >
      Anda belum memposting materi. Silahkan membuat materi baru!
    </v-alert>
  </div>
</template>

<script>
import { onMounted, ref } from "@vue/composition-api";
export default {
  name: "Home",
  props: ["user"],
  setup(props, { root }) {
    const mapel = ref({});
    const expand = ref([0]);
    const length = ref(0);
    const toggleDesc = ref(true);
    const changeDesc = (e) => {
      root.axios
        .post("user/pelajaran/mapel/description", {
          username: props.user.username,
          description: e.target[0].value,
        })
        .then(({ data }) => {
          mapel.value.description = e.target[0].value;
          root.bus.$emit("showSnackbar", {
            message: data.message,
            show: true,
          });
        });
    };
    const getData = () => {
      root.bus.$emit("loadingStart");
      root.axios
        .get("user/pelajaran/mapel/data/" + encodeURI(props.user.username))
        .then(({ data }) => {
          mapel.value = data.mapel;
          length.value = mapel.value.bab.length;
        })
        .catch((err) => err.response)
        .then(() => root.bus.$emit("loadingStop"));
    };
    onMounted(() => {
      getData();
    });
    return { mapel, expand, toggleDesc, changeDesc, length };
  },
};
</script>

